@font-face {
  font-family: 'venice_classicmedium';
  src: url('assets/font/veniceclassic-webfont.woff2') format('woff2'),
       url('assets/font/veniceclassic-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

html {
  background: url("assets/images/background.gif") no-repeat, url("assets/images/background.png") center center;
  background-repeat: no-repeat, repeat-y;
  background-position: top, top;
  background-size: 100% auto, 100% auto;
  overflow-x: hidden;
}

/*#region APP */

.App {
  text-align: center;
  font-family: "venice_classicmedium";
}

.logoNormal {
  width: 400px;
  margin-top: 235px;
}

.logoSmall {
  margin-top: 30px;
  width: 250px;
}

.wallet {
  color: #b41900;
  font-size: 2vw;
  margin: 0px;
  padding-top: 40px;
  margin: 0px 20px 0px 20px;
}

.introArea {
  text-align: center;
}

.intro {
  color: #b08b60;
  font-size: 40px;
  margin: 100px 20% 100px 20%;
}

.footer {
  bottom: 0;
  color: #835f56;
  font-size: 35px;
  margin: 5% 14% 5% 14%;
  margin-top: -200px;
}

.button {
  background: url("assets/images/button.png");
  background-size: cover;
  width: 436px;
  height: 86px;
  font-size: 60px;
  line-height: 5rem; 
  padding: 0px;
  text-align: center;
  color: #b08b60;
  cursor: pointer;
  margin: auto;
  text-align: center;
  display: table;
  overflow: hidden;
}

#statuesContainer {
  margin-top: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* #endregion */

/* #region SELECT ORDER */

.selectText {
  color: #b08b60;
  font-size: 50px;
  text-align: center;
  margin-top: 120px;
  margin-bottom: 0px;
}

.selectionContainer {
  display: table;
  width: 100%;
  margin: 0px 0px 300px 0px;
}

.selectItem {
  display: inline-block;
  color: #b08b60;
  font-size: 40px;
  text-align: center;
  margin: 0px 50px 0px 50px;
}

.selectImage {
  width: 300px;
  cursor: pointer;
  opacity: 0.7;
}

.selectImage:hover {
  opacity: 1;
}

/* #endregion */

/* #region COLLECT BLOOD */

.orderText {
  color: #b08b60;
  font-size: 40px;
  text-align: center;
  margin-bottom: 0px;
  padding-bottom: 0px;
  line-height: 0.1;
}

.contributionText {
  padding: 0px;
  margin-top: -20px;
  color: #b41900;
  font-size: 40px;
  margin-bottom: 50px;
}

.buttonSmall {
  background: url("assets/images/button.png");
  background-size: cover;
  width: 290px;
  height: 57px;
  font-size: 35px;
  line-height: 3.5rem; 
  padding: 0px;
  text-align: center;
  color: #b08b60;
  cursor: pointer;
  margin: auto;
  display: table;
  overflow: hidden;
}

.buttonSmall2 {
  background: url("assets/images/button2.png");
  background-size: cover;
  width: 290px;
  height: 57px;
  font-size: 25px;
  line-height: 1.3rem; 
  padding: 0px;
  text-align: center;
  color: #835f56;
  margin: auto;
  display: table;
  overflow: hidden;
}

.buttonSmall3 {
  background: url("assets/images/button2.png");
  background-size: cover;
  width: 290px;
  height: 57px;
  font-size: 25px;
  line-height: 3.5rem; 
  padding: 0px;
  text-align: center;
  color: #835f56;
  margin: auto;
  display: table;
  overflow: hidden;
}

.plusBlood {
  color: #b08b60;
  font-size: 22px;
  text-align: center;
  margin-top: 5px;
}

.plusBloodDone {
  color: #b41900;
  font-size: 22px;
  text-align: center;
  margin-top: 5px;
}

.missionContainer {
  margin: 20px;
}

.referralText {
  color: #b08b60;
  font-size: 40px;
  margin: 50px 0px 0px 0px;
}

.referralLink {
  color: #b41900;
  font-size: 50px;
  margin: 10px 0px 20px 0px;
}

/* #endregion */

/* #region RPG WINDOW */

.rpgWindow {
  margin: 130px 15% 500px 15%;
}

#rpgTextBox {
  color: #b08b60;
  font-size: 30px;
  text-align: justify;
  line-height: normal;
  overflow-y: auto;
  height: 400px;
  padding: 0px 30px 0px 30px;
}

.chooseDialogContainer {
  display: table;
  width: 100%;
  margin: 50px 0px 100px 0px;
}

.chooseItem {
  display: inline-table;
  color: #b08b60;
  font-size: 30px;
  text-align: center;
  margin: 0px 10px 0px 10px;
}

.chooseButton {
  background: url("assets/images/button-large.png");
  background-size: cover;
  width: 436px;
  height: 119px;
  font-size: 35px;
  line-height: 2.5rem; 
  padding: 0px;
  text-align: center;
  color: #b08b60;
  cursor: pointer;
  margin: auto;
  display: table;
  overflow: hidden;
}

.buttonText {
  display: table-cell; 
  vertical-align: middle;
}

.typeDialogContainer {
  margin: 50px 0px 300px 0px;
}

.typeDialogText {
  color: #b08b60;
  font-size: 45px;
}

#typeField {
  margin: 30px 0px 30px 0px;
  height: 50px;
  width: 60%;
  background-color: #00000000;
  font-family: "venice_classicmedium";
  color: #b08b60;
  text-align: center;
  font-size: 40px;
  border-bottom: 5px dashed #835F56;
  border-top: none;
  border-left: none;
  border-right: none;
}

#typeField::placeholder {
  color: #835f56;
}

textarea:focus, input:focus{
  outline: none;
}

/* width */
div::-webkit-scrollbar {
  width: 1px;
}

/* Track */
div::-webkit-scrollbar-track {
  background: #00000000;
}

/* Handle */
div::-webkit-scrollbar-thumb {
  background: #00000000;
}

/* Handle on hover */
div::-webkit-scrollbar-thumb:hover {
  background: #00000000;
}

/* #endregion */

.playBtn {
  background: url("assets/images/play.png");
  background-size: cover;
  width: 36px;
  height: 36px;
  cursor: pointer;
  position: absolute;
  margin-left: 90%;
  margin-top: 1%;
}

.pauseBtn {
  background: url("assets/images/pause.png");
  background-size: cover;
  width: 36px;
  height: 36px;
  cursor: pointer;
  position: absolute;
  margin-left: 90%;
  margin-top: 1%;
}